
.vertical{
    display:flex;
    flex-direction: row;
    border-bottom: 1px  rgb(244,244,244) solid;
    margin: 1vw;
    padding: 2vw;
    background-color:white;
}
.count{
    color: rgb(135,135,135)
}
.app-img{
    margin: 2vw;
}
h3{
    margin:5vw 1vw;
    color: rgb(179,179,179);
}
.vertical>img{
    width:25%;
    height:25%;
}
.odd { 
    border-radius: 10%;
}
.even {
    border-radius: 50%;
}
.app-info{
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 1vw;
    line-height: 1.8;
}
.app-rating{
    text-align: left;
}
.genres {
    color:rgb(123,123,124)

}
